import Vue from "vue";
import Router from "vue-router";
import Home from "../views/Home.vue";
import SignInPage from "../views/SignInPage.vue";
import SignUpPage from "../views/SignUpPage.vue";
import AboutAuthor from "../views/AboutAuthor.vue";
import StartPage from "../views/StartPage.vue";
import PostWatch from "../views/PostWatch.vue";
import LabelList from "../views/LabelList.vue";
import AdminPanel from "../views/AdminPanel/AdminPanel.vue";
import AdminCreatePost from "../views/AdminPanel/AdminCreatePost.vue";
import AdminPostsEdit from "../views/AdminPanel/AdminPostsEdit.vue";
import AdminPostsList from "../views/AdminPanel/AdminPostsList.vue";
import EditAuthorInfo from "../views/AdminPanel/EditAuthorInfo.vue";
import AdminMainInformation from "../views/AdminPanel/AdminMainInformation.vue";
import NotFoundPage from "../views/NotFoundPage.vue";

Vue.use(Router);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    beforeEnter(to, from, next) {
      let currentUser = JSON.parse(window.localStorage.currentUser);
      if (currentUser && currentUser.logIn) {
        next();
      } else {
        next("/start-page");
      }
    },
  },
  {
    path: "/start-page",
    name: "StartPage",
    component: StartPage,
  },
  {
    path: "/sign-in",
    name: "SignIn",
    component: SignInPage,
  },
  {
    path: "/sign-up",
    name: "SignUp",
    component: SignUpPage,
  },
  {
    path: "/about-author",
    name: "AboutAuthor",
    component: AboutAuthor,
    beforeEnter(to, from, next) {
      let currentUser = JSON.parse(window.localStorage.currentUser);
      if (currentUser && currentUser.logIn) {
        next();
      } else {
        next("/start-page");
      }
    },
  },
  {
    path: "/admin-panel",
    name: "AdminPanel",
    component: AdminPanel,
    params: true,
    beforeEnter(to, from, next) {
      let currentUser = JSON.parse(window.localStorage.currentUser);
      if (currentUser && currentUser.admin) {
        next();
      } else {
        next("/");
      }
    },
    children: [
      {
        path: "/admin-panel/main",
        component: AdminMainInformation,
      },
      {
        path: "/admin-panel/posts",
        name: "AdminPostsList",
        component: AdminPostsList,
      },
      {
        path: "/admin-panel/edit-author-info",
        name: "EditAuthorInfo",
        component: EditAuthorInfo,
      },
    ],
  },
  {
    path: "/post/:id",
    name: "PostWatch",
    component: PostWatch,
    params: true,
    beforeEnter(to, from, next) {
      let currentUser = JSON.parse(window.localStorage.currentUser);
      if (currentUser && currentUser.logIn) {
        next();
      } else {
        next("/start-page");
      }
    },
  },
  {
    path: "/admin-panel/posts/add-post",
    name: "AdminCreatePost",
    component: AdminCreatePost,
    params: true,
    beforeEnter(to, from, next) {
      let currentUser = JSON.parse(window.localStorage.currentUser);
      if (currentUser && currentUser.admin) {
        next();
      } else {
        next("/");
      }
    },
  },
  {
    path: "/admin-panel/posts/edit/:id",
    name: "AdminPostsEdit",
    component: AdminPostsEdit,
    params: true,
    beforeEnter(to, from, next) {
      let currentUser = JSON.parse(window.localStorage.currentUser);
      if (currentUser && currentUser.admin) {
        next();
      } else {
        next("/");
      }
    },
  },
  {
    path: "/:label",
    name: "Label",
    component: LabelList,
    params: true,
    beforeEnter(to, from, next) {
      let currentUser = JSON.parse(window.localStorage.currentUser);
      if (currentUser && currentUser.logIn) {
        next();
      } else {
        next("/start-page");
      }
    },
  },
  {
    path: "/404",
    alias: '*',
    component: NotFoundPage,
  },
];

const router = new Router({
  hashbang: false,
  mode: "history",
  routes,
});

export default router;
