import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    users: [],
    posts: [],
    labels: [],
    currentUser: {},
    admin: "",
    authorInfo: {},
    authorInfoGitHub: {},
  },
  getters: {},
  mutations: {
    setPosts(state, posts) {
      state.posts = posts;
      for (let unigue of [].concat.apply(
        [],
        state.posts.map((post) => post["lablesList"])
      )) {
        if (!state.labels.includes(unigue)) {
          state.labels.push(unigue);
        }
      }
    },
    setUsers(state, users) {
      state.users = users;
    },
    setAdminLogin(state, admin) {
      state.admin = admin;
    },
    setAuthorInfo(state, author) {
      state.authorInfo = author;
    },
    setInfoAboutAuthorFromGithub(state, payload) {
      state.authorInfoGitHub = {
        avatar_url: payload.avatar_url,
        bio: payload.bio,
        location: payload.location,
      };
    },
    addPost(state, post) {
      let posts = state.posts.concat(post);
      state.posts = posts;
    },
    addUser(state, user) {
      let users = state.users.concat(user);
      state.users = users;
    },
    deletePostFromState(state, postId) {
      let posts = state.posts.filter((post) => post.id != postId);
      state.posts = posts;
    },
    setEditPost(state, editedPost) {
      state.posts.forEach((post) => {
        if (post.id == editedPost.id) {
          post = editedPost;
        }
      });
    },
    setInfoAboutAuthor(state, editedinfo) {
      state.authorInfo = editedinfo;
    },
    logoutUser(state) {
      state.currentUser = {};
      window.localStorage.currentUser = JSON.stringify({});
    },
    setCurrentUser(state, userInfo) {
      if (userInfo.logIn === state.admin.login) {
        state.currentUser = {
          logIn: userInfo.logIn,
          password: userInfo.password,
          admin: true,
        };
        window.localStorage.currentUser = JSON.stringify({
          logIn: userInfo.logIn,
          password: userInfo.password,
          admin: true,
        });
        return;
      }
      state.currentUser = userInfo;
      window.localStorage.currentUser = JSON.stringify(userInfo);
    },
  },
  actions: {
    async getPosts({ commit }) {
      let response = await axios.get("http://localhost:3000/posts");
      commit("setPosts", response.data);
    },
    async createPost({ commit }, post) {
      let response = await axios.post("http://localhost:3000/posts", post);
      let addedPost = response.data;
      commit("addPost", addedPost);
      return addedPost;
    },
    async deletePost({ commit }, post) {
      let response = await axios.delete(
        `http://localhost:3000/posts/${post.id}`
      );
      if (response.status == 200 || response.status == 204) {
        commit("deletePostFromState", post.id, post);
      }
      return;
    },
    async editPost({ commit }, post) {
      let response = await axios.put(
        `http://localhost:3000/posts/${post.id}`,
        post
      );
      let editedPost = response.data;
      commit("setEditPost", editedPost);
      return editedPost;
    },
    async editInfoAboutAuthor({ commit }, info) {
      let response = await axios.put("http://localhost:3000/adminInfo", info);
      let editedinfo = response.data;
      commit("setInfoAboutAuthor", editedinfo);
      return editedinfo;
    },
    async getUsers({ commit }) {
      let response = await axios.get("http://localhost:3000/users");
      commit("setUsers", response.data);
      let user = JSON.parse(window.localStorage.currentUser);
      commit("setCurrentUser", user);
    },
    async getAdminLoginInfo({ commit }) {
      let response = await axios.get("http://localhost:3000/adminCredentials");
      commit("setAdminLogin", response.data);
      let admin = JSON.parse(window.localStorage.currentUser);
      commit("setCurrentUser", admin);
    },
    async getInfoAboutAuthorFromGithub({ commit }) {
      let response = await axios.get(
        "https://api.github.com/users/boikodaria22"
      );
      commit("setInfoAboutAuthorFromGithub", response.data);
    },
    async getAuthorInfo({ commit }) {
      let response = await axios.get("http://localhost:3000/adminInfo");
      commit("setAuthorInfo", response.data);
    },
    logoutUserAction({ commit }) {
      commit("logoutUser");
    },
    loginUser({ commit }, userInfo) {
      commit("setCurrentUser", userInfo);
      return userInfo;
    },
    async registrUser({ commit }, userRegistrInfo) {
      let response = await axios.post(
        `http://localhost:3000/users`,
        userRegistrInfo
      );
      commit("addUser", response.data);
      commit("setCurrentUser", {
        logIn: userRegistrInfo.logIn,
        password: userRegistrInfo.password,
      });
    },
  },
  modules: {},
});
